/* CreateGame.css */

body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background: linear-gradient(to bottom, #1a1f33, #0d1127);
    color: #ffffff;
    display: flex;
    align-items: center;
}

.create-game-container {
    text-align: center;
    width: 90%;
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    background: rgba(34, 41, 64, 0.8);
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.create-game-header {
    margin-bottom: 2rem;
}

.interactive-title {
    font-size: 3rem;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: #ffcc00;
    text-shadow: 0 0 15px #ffcc00, 0 0 25px #ffb700;
    animation: glow 2s infinite alternate;
}

@keyframes glow {
    from {
        text-shadow: 0 0 15px #ffcc00, 0 0 25px #ffb700, 0 0 35px #ff9f00;
    }
    to {
        text-shadow: 0 0 20px #ffcc00, 0 0 30px #ffb700, 0 0 40px #ff9f00;
    }
}

.subtitle {
    font-size: 1.2rem;
    color: #cccccc;
    margin-bottom: 2rem;
}

.error-message {
    background: #ff4d4d;
    color: white;
    padding: 1rem;
    margin: 1rem 0;
    border-radius: 8px;
    animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.form-container {
    margin-top: 2rem;
}

.create-button {
    font-size: 1.5rem;
    padding: 1rem 2rem;
    background: linear-gradient(135deg, #ffcc00, #ff9900);
    color: #222940;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
}

.create-button:hover {
    background: linear-gradient(135deg, #ffdd33, #ffaa33);
    transform: scale(1.05);
}

.create-button:disabled {
    background: #777;
    cursor: not-allowed;
}
