/* Lobby.css - A colorful and game-like design for the Monopoly game lobby */

body {
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #2c3e50, #3498db);
    color: white;
    margin: 0;
    padding: 0;
}

.lobby-container {
    text-align: center;
    padding: 2rem;
    margin: auto;
    max-width: 600px;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    border: 2px solid #f1c40f;
}

h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: #f1c40f;
    text-shadow: 2px 2px #2c3e50;
}

h3 {
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #ecf0f1;
}

.players-list {
    margin: 1rem 0;
    background: rgba(255, 255, 255, 0.1);
    padding: 1rem;
    border-radius: 10px;
}

.players-list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.players-list li {
    font-size: 1.2rem;
    margin: 0.5rem 0;
    color: #ecf0f1;
    text-shadow: 1px 1px #2c3e50;
}

.error-message {
    background: #e74c3c;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    margin-bottom: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.lobby-controls {
    margin-top: 2rem;
}

button {
    background: #e67e22;
    color: white;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: all 0.3s;
}

button:hover {
    background: #d35400;
    transform: scale(1.05);
}

button:disabled {
    background: #7f8c8d;
    cursor: not-allowed;
}

@media (max-width: 600px) {
    .lobby-container {
        padding: 1rem;
    }

    h2 {
        font-size: 2rem;
    }

    h3 {
        font-size: 1.25rem;
    }

    button {
        font-size: 0.9rem;
        padding: 0.5rem 1rem;
    }
}
