/* Main.css */

body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(135deg, #1e3a8a, #2c7ce0);
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
}

.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 2rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    max-width: 700px;
    width: 90%;
    animation: fadeIn 1s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.main-header {
    margin-bottom: 2rem;
}

.interactive-title {
    font-size: 3rem;
    text-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    color: #ffcc00;
    display: inline-block;
    position: relative;
}

.interactive-title .title-part {
    display: inline-block;
    animation: titleGlow 2s infinite alternate;
}

@keyframes titleGlow {
    from {
        text-shadow: 0 0 10px #ffcc00, 0 0 20px #ffcc00, 0 0 30px #ffcc00;
    }
    to {
        text-shadow: 0 0 20px #ffe066, 0 0 30px #ffe066, 0 0 40px #ffe066;
    }
}

.subtitle {
    font-size: 1.2rem;
    margin-top: 0.5rem;
    color: #e0e0e0;
}

.content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 100%;
}

h2 {
    color: #ffcc00;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.join-game-section, .create-game-section {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(8px);
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.input-group {
    margin-bottom: 1rem;
}

input {
    width: 100%;
    padding: 0.75rem 0 0.75rem 0;
    border: none;
    border-radius: 5px;
    outline: none;
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
    transition: all 0.2s ease-in-out;
}

input:focus {
    background: #ffffff;
    box-shadow: 0 0 8px #ffcc00;
}

button {
    background: linear-gradient(45deg, #ff9a00, #ffcc00);
    border: none;
    color: #1e3a8a;
    padding: 0.75rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.2s, box-shadow 0.2s;
}

button:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 8px rgba(255, 255, 0, 0.5);
}

.error-message {
    color: #ff4d4d;
    font-size: 0.9rem;
    margin-top: -0.5rem;
    margin-bottom: 0.5rem;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.main-footer {
    margin-top: 2rem;
    font-size: 0.9rem;
    color: #e0e0e0;
}

.main-footer a {
    color: #ffcc00;
    text-decoration: none;
    font-weight: bold;
}

.main-footer a:hover {
    text-decoration: underline;
}