.result-page {
    text-align: center;
    padding: 20px;
    background-color: #222940;
    color: #ffffff;
    min-height: 100vh;
}

.game-over-title {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #ffd700;
}

.podium {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 30px 0;
}

.podium-place {
    text-align: center;
    padding: 20px;
    border-radius: 8px;
    background-color: #34495e;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.5s ease;
}

.place-1 {
    transform: translateY(-20px);
    background-color: #ffd700;
    color: #000;
}

.place-2 {
    background-color: #c0c0c0;
    color: #000;
}

.place-3 {
    background-color: #cd7f32;
    color: #000;
}

.leaderboard {
    margin-top: 40px;
}

.leaderboard h2 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #ffcc00;
}

.leaderboard ul {
    list-style: none;
    padding: 0;
}

.leaderboard-item {
    margin: 10px 0;
    padding: 10px;
    background-color: #34495e;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    color: #ffffff;
}

.leaderboard-item.top-3 {
    font-weight: bold;
    color: #ffd700;
}

.home-button {
    margin-top: 30px;
    padding: 10px 20px;
    font-size: 1.2em;
    background-color: #ffcc00;
    color: #222940;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.home-button:hover {
    background-color: #ffd700;
}
